@import url("https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@200&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@300&family=Roboto+Mono:wght@200&display=swap");
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Roboto Mono", "Noto Sans TC",
    "Roboto", "Roboto Mono", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #0a192f;
  overflow-x: hidden;
  position: relative;
}
